import React, { Component } from "react";

class Resume extends Component {
  getRandomColor() {
    let letters = "0123456789ABCDEF";
    let color = "#";
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  render() {
    if (!this.props.data) return null;

    const skillmessage = this.props.data.skillmessage;
    const education = this.props.data.education.map(function(education) {
      return (
        <div key={education.school}>
          <h3>{education.school}</h3>
          <p className="info">
            {education.degree} 
            <em className="date">  {education.graduated}</em>
          </p>
          <p>{education.description}</p>
        </div>
      );
    });

    const work = this.props.data.work.map(function(work) {
      return (

       <div key={work.company}>
          
         <h3>{work.company}</h3>
          <p className="info">
            {work.title}
            <span>&bull;</span> <em className="date">{work.years}</em>
          </p>
          <p>{work.description}</p> 


        </div>
      );
    });

    const skills = this.props.data.skills.map(skills => {
      const backgroundColor = this.getRandomColor();
      const className = "bar-expand " + skills.name.toLowerCase();
      const width = skills.level;

      return (
        <li key={skills.name}>
          <span style={{ width, backgroundColor }} className={className}></span>
          <em>{skills.name}</em>
        </li>
      );
    });

    return (
      <section id="resume">
        <div className="row education">
          <div className="three columns header-col">
            <h1>
              <span>Education</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">{education}</div>
            </div>
          </div>
        </div>

        <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Work</span>
            </h1>
          </div>
          
                    <div className="nine columns main-col"> {/*} {work} */} 
                    <h1>Purchasing Manager</h1>
                    <b>MCG Electronics &emsp;&bull; 2017 — Present</b>
                    <br />
                    <p>&bull; Oversee and implement entire procurement process from sourcing to purchase order fulfillment. Procurement includes but not limited to all raw material, custom parts, electronic components, working tools, facility devices, maintenance and control services. This process is performed to be in strict compliance with ISO 9001 manufacturing certification guidelines. 
                    <br />&bull; Perform routine inventory/vendor/cost analyses and forecasts via Key Performance Indicators, Material Requirement Planning, and database management to present to and coordinate with other departments.
                    <br />&bull; Build and maintain strong, long-lasting vendor relationships.
                    <br />&bull; Consistently perform under pressure of deadlines.
                    <br />&bull; Participate in national trade show presentations.
                    <br />&bull; Initiated and executed an obsolete stock liquidation program.
                    <br />&bull; Updated purchasing department technology, replacing thirty-year-old technology with a virtually paperless cloud-based environment.
                    </p>
                    <h1>Account Executive</h1>
                    <b>Florence Building Materials &emsp;&bull; 2015 — 2017</b>
                    <br />
                    <p>&bull; Managed client relationships for the duration of building projects, from early stages of research and discovery to the final stages of delivery and installation. 
                    <br />&bull; Analyzed customer requirements and utilized expert knowledge learned in the field as a project manager to recommend customized client solutions.
                    <br />&bull; Monitored project costs to ensure they were aligned with budgets.
                    <br />&bull; Operated as initial point of contact for the company, servicing new accounts as well as maintaining existing accounts.
                    <br />&bull; Cultivated new accounts via soft lead management platforms.
                    </p>
                    <h1>Project Manager</h1>
                    <b>All Pro Remodeling &emsp;&bull; 2008 — 2015</b>
                    <br />
                    <p>&bull; Coordinated and delivered multiple, simultaneous remodel and rehabilitation construction projects through the five boroughs of New York City, Long Island, Upstate New York and New Jersey. 
                    <br />&bull; Maintained databases of proposals, invoices, job costs, and pertinent client information.
                    <br />&bull; Estimated projects based on cost analyses, site visitations and client requirements.
                    <br />&bull; Contracted, scheduled and oversaw delivery of all subcontractors.
                    <br />&bull; Fostered key client relationships for the company that are still active and relevant for the company today.
                    <br />&bull; Coordinated and obtained necessary permits for the successful completion of jobs.
                    </p>


                    </div>
                  </div>
          
                  <div className="row skill">
                    <div className="three columns header-col">
            <h1>
              <span>Skills</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <p>{skillmessage}</p>

            <div className="bars">
              <ul className="skills">{skills}</ul>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Resume;
